import React, { useRef, useEffect } from "react";
import { gsap } from "gsap";
import { useNavigate, userNavigate } from 'react-router-dom'

const HomePage = () => {

    const titleRef1 = useRef(null);
    const titleRef2 = useRef(null);
    const bgUpRef = useRef(null);
    const bgDownRef = useRef(null);
    const exploreLinkRef = useRef(null);
    const Navigate = useNavigate();

    useEffect(() => {

        gsap.fromTo(bgUpRef.current, { y: -500 }, { duration: 1.3, y: 0 });

        gsap.fromTo(titleRef1.current, { x: 1200 }, { duration: .5, x: 0 });
        gsap.fromTo(titleRef2.current, { x: 1200 }, { duration: 1, x: 0 });

        gsap.fromTo(exploreLinkRef.current, { opacity: 0 }, { opacity: 1, duration: 2 });

    }, []);

    const handleExploreLink = () => {

        gsap.to(bgUpRef.current, { opacity: 0, duration: 1 });

        gsap.fromTo(titleRef1.current, { x: 0 }, { duration: 1, x: 1200 });
        gsap.fromTo(titleRef2.current, { x: 0 }, { duration: 1, x: 1200 });
        gsap.to(exploreLinkRef.current, { opacity: 0, duration: 1 });
        
        setTimeout(() => {
            Navigate('/quicktour');
        }, 600)
    }

    return (
        <>
            <div className='bg-container'>
                <div className='bg-up' ref={bgUpRef}></div>
                <div className='bg-down' ref={bgDownRef} ></div>

                <button
                    className="custom-link"
                    id="home-explore-link"
                    ref={exploreLinkRef}
                    onClick={handleExploreLink}
                >Explorer</button>

                {/* <Link
                    to='/quicktour'
                    className='custom-link'
                    ref={exploreLinkRef}
                >Explorer</Link> */}
            </div>
            <div className="main-title-container">
                <h1
                    className="main-title"
                    ref={titleRef1}
                    id='title-calm'>CALM</h1>
                <h1
                    className="main-title"
                    ref={titleRef2}
                    id='title-night'>NIGHT</h1>
            </div>
        </>
    )
}

export default HomePage;